.Box-conatiner {
    display: flex;
    justify-content: center;
}

.Box-1 {

    width: 200px;
    height: 200px;
    border: 1px solid black;
    background-color: rgba(243, 226, 226, 0.591);

}

.Box-conatiner-2 {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    text-align: center;

}

.Box-2 {
    width: 100px;
    height: 100px;
    border: 1px solid black;
    border-radius: 50%;

}

.Button-conatiner {
    display: flex;
    justify-content: center;
    padding-top: 1%;

}
p{
    font-size:40%;
    text-decoration: solid;
     text-decoration: underline;
}
